@import 'variables.scss';

/******************************
 Login Page
*******************************/

.login-box {
  display: flex;
  > div {
    width: 100%;
    width: -webkit-fill-available;
    width: -moz-available;
  }
  .login-box{
    .form-group{
      flex-wrap: wrap;
    }
  }
  &::before,
  &::after {
    display: none;
  }
  .well {
    height: 100%;
    border: none;
    border-radius: 0;
    padding: 0;
    margin: 0;
    box-shadow: none;
    background: transparent;
    display: flex;
    flex-direction: column;
    > .btn {
      margin-top: auto;
      align-self: flex-start;
    }
    p:first-of-type {
      display: none;
    }
    .buttons {
      margin-top: auto;
    }
  }
}


#account-login .well{
  margin:0;
}

.form-group input[type="password"] + div {
  margin-top: 10px;
  margin-bottom: 10px;
  position: relative;
  width: 100%;
  flex-grow: 1;
}

.register-page{
  > div {
    width: 100%;
  }
  > p{
    margin-bottom: 20px;
  }
  .fieldset + fieldset{
    margin-top: 30px;
  }
}

/******************************
 My Account
*******************************/

.route-account-account,
.route-affiliate-account {
  #content {
    display: flex;
    flex-direction: column;
    > div {
      margin-bottom: 25px;
    }
  }
}

.account-list {
  display:flex;
  flex-wrap: wrap;
  > li {
    width: 100%;
    margin-bottom: 5px;
    display: flex;
    > a {
      display: inline-flex;
      align-items: center;
      line-height: 1.3;
      transition: all 0.1s ease-out;
      &::before {
        margin-right: 5px;
      }
    }
  }
}

.route-account-return-add{
  fieldset{
    margin-top: 30px;
  }
}

.route-account-order-info{
  table{
    margin-bottom: 20px;
  }
  h2{
    margin-top: 20px;
  }
}

.route-account-return-info{
  h1 + table,
  .table-responsive{
    margin-bottom: 20px;
  }
}

#account, #address, #account-address form > div:not(.buttons){
  display:flex;
  flex-direction:column;
}

