@import '../variables.scss';

/******************************
 Main Menu
*******************************/

.desktop-main-menu-wrapper {
  position: static;
  &::before {
    content: "";
    position: absolute;
    width: 100vw;
    left: 50%;
    margin-left: -50vw;
    bottom: 0;
    z-index: -1;
    height: 100%;
  }

  .main-menu {
    height: 100%;
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    //second row items normal width
    &::after {
      //content: '';
      flex-grow: 99;
    }
    .main-menu-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      > a {
        border-width: 0 0 0 1px;
        justify-content: center;
      }
      &:first-of-type {
        border-top-left-radius: inherit;
        border-bottom-left-radius: inherit;
        > a {
          border-left-width: 0;
          border-radius: inherit;
        }
      }
      &:last-of-type {
        border-top-right-radius: inherit;
        border-bottom-right-radius: inherit;
        > a {
          border-right-width: 0;
          border-radius: inherit;
        }
      }
      &.dropdown {
        > a {
          &::after {
            transform: rotate(90deg);
            margin: 0 5px;
          }
        }
      }
    }
    > .j-menu {
      border-radius: inherit;
      flex-wrap: nowrap;
      > li > a {
        .links-text {
          width: 100%;
        }
      }
    }
  }

  &.menu-stretch {
    .main-menu {
      width: 100%;
      flex-wrap: nowrap;
      > ul {
        width: 100%;
        > li {
          flex-grow: 1;
        }
      }
    }
  }

  &.menu-center {
    text-align: center;
    .main-menu {
      margin: 0 auto;
      display: inline-flex;
    }
  }

  &.has-menu-2 {
    display: flex;
    align-items: center;
    //justify-content: space-between;
  }
}

html[dir='rtl']{
  .desktop-main-menu-wrapper{
    .main-menu .main-menu-item{
      > a{
        border-width: 0 1px 0 0;
      }
      &:first-of-type > a{
        border-right-width: 0;
      }
    }
  }
}

html[dir='rtl'] .desktop-main-menu-wrapper .main-menu>.j-menu>.first-dropdown::before{
  display: none !important;
}

/******************************
 Flyout
*******************************/

.main-menu > .flyout {
  .flyout-menu-item:first-child,
  .flyout-menu-item:first-child > a {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
  }
  .flyout-menu-item:last-child,
  .flyout-menu-item:last-child > a {
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
  }
  > .dropdown-menu::before {
    left: 10px;
  }
}

/******************************
 Mega Menu
*******************************/

$pad: 20px;

.desktop-main-menu-wrapper {
  &.menu-center #main-menu{
    margin: 0 auto;
  }
  .main-menu {
    .mega-menu {
      &.mega-custom {
        position: relative;
      }
      .mega-menu-content {
        max-height: calc(100vh - 100px);
        margin: 0 auto;
      }
    }
    .menu-fullwidth {
      .mega-menu-content {
        max-width: 100%;
      }
    }
    .menu-fullwidth,
    .mega-fullwidth {
      > .j-dropdown {
        padding-top: 0;
        &::before {
          display: none;
        }
      }
    }
  }

