/******************************
 Menu Open Site Overlay
*******************************/

@keyframes overlay {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.desktop-main-menu-wrapper {
  .main-menu > .j-menu > .first-dropdown::before {
    z-index: 0;
    position: absolute;
    width: 150vw;
    height: calc(100vh - 60px);
    top: 100%;
    left: 0;
    pointer-events: none;
    animation: overlay .3s ease;
    opacity: 0;
    visibility: hidden;
    transition: all .15s .15s ease-in-out;
    content: "";
    display: none;
  }
  &.boxed-layout {
    .main-menu > .j-menu > .first-dropdown::before {
      display: none !important;
    }
  }
  .main-menu > .j-menu > .first-dropdown.mega-custom::before{
    width: 200vw;
  }
}

.header-sm .desktop-main-menu-wrapper {
  .main-menu > .j-menu > .first-dropdown::before {
    width: 150vw;
  }
}

.desktop-header-active .menu-open .main-menu > .j-menu > .first-dropdown::before {
  opacity: 1;
  visibility: visible;
  display: block;
}

.desktop-header-active .menu-hover .main-menu > .j-menu > .first-dropdown::before {
  opacity: 0;
  visibility: hidden;
  display: none;
}

/******************************
 Mobile Menu Desktop
*******************************/

.no-touchevents {
  &.mobile-menu-open {
    overflow: hidden;
  }
  .mobile-wrapper-open {
    overflow-y: scroll;
  }
}

.desktop-header-active {
  &.mobile-overlay {
    body {
      overflow: scroll;
    }
  }
  .menu-trigger {
    display: none !important;
    ul, li {
      height: 100%;
    }
    a {
      cursor: pointer !important;
      justify-content: flex-start;
      border-width: 0;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 5px 15px;
    }
  }
  &.mobile-menu-active {
    .menu-main-menu-wrapper {
      overflow-y: auto;
    }
    .site-wrapper #main-menu {
      display: none;
    }
    .menu-trigger {
      display: inline-flex !important;
      height: 100%;
    }
    .is-sticky {
      .menu-trigger {
        margin-right: auto;
      }
    }
  }
}





